.form-login {
  align-items: center;
  background-attachment: fixed;
  background-image: url('https://images.pexels.com/photos/3165335/pexels-photo-3165335.jpeg?cs=srgb&dl=pexels-lucie-liz-3165335.jpg&fm=jpg');
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: 100%;
  justify-content: center;
  margin: 0;
  position: fixed;
  width: 100%;
}

.form-login input {
  border-radius: 8px;
  padding: 10px;
  width: 180px;
}

.button-play {
  background-color: #fc008f;
  border: none;
  border-radius: 8px;
  box-shadow: 0 0.2em #a80160;
  color: #430387;
  cursor: pointer;
  font-size: 20px;
  font-weight: 700;
  padding: 16px;
  text-align: center;
  text-shadow: -1px -1px #a80160 , 1px 1px #430387;
  width: 100px;
}

.button-play:hover {
  animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

.button-settings {
  background-color: #430387;
  border: none;
  border-radius: 8px;
  box-shadow: 0 0.2em #7e06ff;
  color: #fc008f;
  cursor: pointer;
  font-size: 20px;
  font-weight: 700;
  padding: 16px;
  text-align: center;
  text-shadow: -1px -1px #7e06ff , 1px 1px #fc008f;
  width: 180px;
}

.button-settings:hover {
  animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

.login-logo {
  animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  height: 15vmin;
  pointer-events: none;
}

.login-header {
  align-items: center;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
}

@keyframes shake {

  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
